import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"

const ContactPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout location={location} title={siteTitle}>
      <h1 className="h1-aboutpage">Contact</h1>
      <h4 className="h4-contact">Social Media</h4>
      <p className="p-contact">
        <a href="https://twitter.com/muku_464939" target="_blank">
          Twitter
        </a>
      </p>
      <h4 className="h4-contact">E-Mail</h4>
      <p className="p-contact">
        <a href="mailto:nanahara.takanori.r3@s.mail.nagoya-u.ac.jp">nanahara.takanori.r3@s.mail.nagoya-u.ac.jp</a>
      </p>
    </Layout>
  )
}

export const Head = () => <Seo title="Contact" />

export default ContactPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`